@import "../../styles/main.less";

.testimonial {
  .testimonial__container-fluid {
    .flex-center();
    background: url('../../../public/images/kylee-alons-suQ-D-9dmVU-unsplash.jpg') no-repeat center center;
    background-size: cover;
    // background-position: 30% 0;
    padding: 10rem 0;

    .testimonial__play-button {
      .flex-center();
      position: relative;
      width: 100px;
      height: 100px;
      background-color: fade(white, 80%);
      border-radius: 100%;
      transition: 0.3s all ease-in-out;
      cursor: pointer;

      &:before {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: fade(#317610, 80%);
        border-radius: 100%;
        animation: pulse-border 1500ms ease-out infinite;
      }

      &:hover {
        background-color: fade(@primary-color, 80%);

        .testimonial__play-icon {
          color: white;
        }
      }

      .testimonial__play-icon {
        color: #317610;
        font-size: 3rem;
        transition: 0.3s all ease-in-out;
      }
    }
  }

  .testimonial__container {
    .flex-center();
    position: relative;
    top: -5rem;
    padding: 0;
    overflow: initial;

    .testimonial__carousel {
      width: 100vw;
      max-width: 800px;
      background-color: white;
      box-shadow: @card-shadow;
      padding: 2rem 2rem 0 2rem;

      .slick-dots li button {
        background: #fff !important;
      } 
      
      .slick-dots li.slick-active button {
        background: @primary-color !important;
      }

      .carousel__item {
        min-height: 20rem;
        text-align: center;
        margin-bottom: 2rem;

        .carousel__item-comment,
        .carousel__item-commenter {
          .text();
          font-style: italic;
          font-size: 1.2rem;
        }

        .carousel__item-commenter {
          font-size: 1.2rem;

          .commenter__name{
            color: black;
            font-style: normal;
          }
        }

        .carousel__item-avatar {
          display: inline-block;
          width: 120px;
          height: 120px;
          border-radius: 100%;
        }
      }
    }
  }

  @keyframes pulse-border {
    0% {
      transform: scale(1);
      opacity:1;
    }

    100% {
      transform: scale(1.5);
      opacity:0
    }
  }
}

.testimonial__modal {
  .ant-modal-body {
    padding: 0 !important;
  }
}

@primary-color: #317610;@link-color: #66b53c;@success-color: #27ae60;@warning-color: #f39c12;@error-color: #e74c3c;@font-size-base: 15px;@heading-color: #121417;@text-color: #2D3239;@text-color-secondary: #3374FF;@disabled-color: rgba(0, 0, 0, 0.25);@border-radius-base: 4px;@border-color-base: #3498db;@box-shadow-base: 0 3 px 6 px - 4 px rgba(0, 0, 0, 0.12),                                                      0 6 px 16 px 0 rgba(0, 0, 0, 0.08),                                                       0 9 px 28 px 8 px rgba(0, 0, 0, 0.05);