@import "../../styles/main.less";

.member {
  .card();

  .ant-card-body {
    position: relative;

    .member__contact {
      position: absolute;
      top: -20%;
      left: 50%;
      transform: translateX(-50%);

      .member__contact-link {
        .flex-center();
        width: 2rem;
        height: 2rem;
        border-radius: 100%;
        border: @border;
        font-size: 1.2rem;
        transition: 0.3s all ease-in-out;
        background-color: white;

        &:hover {
          background-color: @primary-color;
          color: white;  
        }
      }
    }

    .ant-card-meta-title {
      margin-bottom: 0;
    }

    .ant-card-meta-description {
      font-size: 0.8rem;
    }
  }
}
@primary-color: #317610;@link-color: #66b53c;@success-color: #27ae60;@warning-color: #f39c12;@error-color: #e74c3c;@font-size-base: 15px;@heading-color: #121417;@text-color: #2D3239;@text-color-secondary: #3374FF;@disabled-color: rgba(0, 0, 0, 0.25);@border-radius-base: 4px;@border-color-base: #3498db;@box-shadow-base: 0 3 px 6 px - 4 px rgba(0, 0, 0, 0.12),                                                      0 6 px 16 px 0 rgba(0, 0, 0, 0.08),                                                       0 9 px 28 px 8 px rgba(0, 0, 0, 0.05);